import { WeatherService } from './../weather.service';
import { Component, OnInit } from '@angular/core';
import { Weather } from "./weather";
import { WEATHER } from "./weather-data";

@Component({
  selector: 'app-weather',
  templateUrl: './weather.component.html',
  styleUrls: ['./weather.component.css']
})
export class WeatherComponent implements OnInit {
private currentCityWeather : Weather;
private pastCityWeathers : Weather [] = [];
  constructor(private weatherservice: WeatherService) { }

  ngOnInit() {
  }
  getWeather(cityName: string):void{
    this.currentCityWeather = this.weatherservice.getWeather(cityName);
    this.currentCityWeather.searchTime = (new Date()).toUTCString();
    this.pastCityWeathers.push(this.currentCityWeather);
    //for(var i=0; i < WEATHER.length;i++)
    //{
    // if(WEATHER[i].cityName == cityName)
    //  this.currentCityWeather=WEATHER[i];
    //}
  }
}



